import { Component, HostListener, Input, OnInit, SimpleChanges } from '@angular/core';
import { Router } from '@angular/router';
import * as $ from 'jquery';
import { Reports } from 'src/app/models/reports.model';
import { AuthService } from 'src/app/services/auth.service';
import { ReportsService } from 'src/app/services/reports.service';

@Component({
  selector: 'app-tab',
  templateUrl: './tab.component.html',
  styleUrls: ['./tab.component.scss']
})
export class TabComponent implements OnInit {



  report: Array<Reports>;
  repo: any = [];

  @Input() reportId: number;

  reporterName: string;
  reporterSurname: string;
  reportTitle: string;
  notes: string;
  reportTime: Date;
  status: string;
  priority: number;
  attachments: string;
  username: string;
  lista: any;
  dettagli: any;

  constructor(private reportsService: ReportsService, private router: Router, public authService: AuthService) { }

  // @HostListener('window:popstate', ['$event'])
  // onPopState(event) {
  //   this.router.navigate(['/settings']);
  //   console.log('Back button pressed');
  // }

  hide(){
    this.lista = true;
    this.dettagli = false;
    this.colonna();
    // console.log('metodo hide');
  }

  AggiornaReportsNew(): void {
    this.report = [];
    this.repo = [];
    this.reportsService.GetAllNew().subscribe(data => {
      this.report = data;
      console.log(this.report);
      this.colonna();
      this.pagina();
    });
    this.hide();
  }

  AggiornaReportsOnGoing(): void {
    this.report = [];
    this.repo = [];
    this.reportsService.GetAllOngoing().subscribe(data => {
      this.report = data;
      console.log(this.report);
    });
    this.hide();
  }

  AggiornaReportsCompleted(): void {
    this.report = [];
    this.repo = [];
    this.reportsService.GetAllClosed().subscribe(data => {
      this.report = data;
      console.log(this.report);
    });
    this.hide();
  }

  AggiornaReportsDeleted(): void {
    this.report = [];
    this.repo = [];
    this.reportsService.GetAllDeleted().subscribe(data => {
      this.report = data;
      console.log(this.report);
    });
    this.hide();
  }

  GetReports(reportId: number): void {
    this.reportsService.GetSingle(reportId).subscribe(data => {
      this.repo = [];
      this.reportId = data.ReportId;
      this.reporterName = data.reporterName;
      this.reporterSurname = data.reporterSurname;
      this.reportTime = data.reportTime;
      this.repo.push(data);
      console.log(this.repo);
    });
    this.lista = false;
    this.dettagli = true;
  }

  ChangeInOngoing(reportId: number) {
    this.reportsService.PutOngoing(reportId).subscribe(p => {
      this.AggiornaReportsOnGoing();
    });
  }

  ChangeInClosed(reportId: number) {
    this.reportsService.PutClosed(reportId).subscribe(p => {
      this.AggiornaReportsCompleted();
    });
  }

  ChangeInDeleted(reportId: number) {
    this.reportsService.PutDeleted(reportId).subscribe(p => {
      this.AggiornaReportsDeleted();
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.AggiornaReportsNew();
    this.AggiornaReportsCompleted();
    this.AggiornaReportsOnGoing();
    this.AggiornaReportsDeleted();
  }



  LogOut(): void {
    this.authService.LogOut();
    this.authService.CheckUser();
  }


  pagina(): void {
    // $(function() {
    //   const rowsPerPage = 10;
    //   const rows = $('#my-table tbody tr');
    //   const rowsCount = rows.length;
    //   const pageCount = Math.ceil(rowsCount / rowsPerPage); // avoid decimals
    //   var numbers = $('#numbers');

    //   // Generate the pagination.
     
    //     for (var i = 0; i < pageCount; i++) {
    //       numbers.append('<li><a>' + (i+1) + '</a></li>');
    //   }

    // });
  }


  colonna(): void {
    $(function() {
      const rowsPerPage = 10;
      const rows = $('#my-table tbody tr');
      const rowsCount = rows.length;
      const pageCount = Math.ceil(rowsCount / rowsPerPage); // avoid decimals
      var numbers = $('#numbers');
      for (var i = 0; i < pageCount; i++) {
        numbers.append('<li><a>' + (i+1) + '</a></li>');
    }

      // Mark the first page link as active.
      $('#numbers li:first-child a').addClass('active');

      // Display the first set of rows.
      displayRows(1);

      // On pagination click.
      $('#numbers li a').click(function(e) {
        var $this = $(this);

        e.preventDefault();
        
        // Remove the active class from the links.
        $('#numbers li a').removeClass('active');
        
        // Add the active class to the current link.
        $this.addClass('active');
        
        // Show the rows corresponding to the clicked page ID.
        displayRows($this.text());
      });
      
      // Function that displays rows for a specific page.
      function displayRows(index) {
        var start = (index - 1) * rowsPerPage;
        var end = start + rowsPerPage;
        
        // Hide all rows.
        rows.hide();
        
        // Show the proper rows for this page.
        rows.slice(start, end).show();
      }
    });
  }

  ngOnInit(): void {
    this.AggiornaReportsNew();
    this.authService.GetUsername();
    this.username =  this.authService.GetUsername();
    
  }
}