import { Component, OnInit, SimpleChanges } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { Utenti } from 'src/app/models/user.model';
import { AuthService } from 'src/app/services/auth.service';
import { UsersService } from 'src/app/services/users.service';

@Component({
  selector: 'app-settingscontent',
  templateUrl: './settingscontent.component.html',
  styleUrls: ['./settingscontent.component.scss']
})
export class SettingscontentComponent implements OnInit {

  users: Array<Utenti>;
  userName:string;
  username: string;
  password: number;
  name: string;
  surname: string;
  id: string;
  email: string;
  user: any;
  p:any;
  pc:any;
  confirmPassword: string;

  constructor(public authService: AuthService, public usersService: UsersService, private router: Router) { }

  LogOut():void {
    this.authService.LogOut();
    this.authService.CheckUser();
  }


  EditUser(): void {
    this.user = this.username;
    this.p = {
      OldPassword: this.password,
      NewPassword: this.confirmPassword
    }
    this.usersService.Put(this.user,this.p).subscribe(data=>{
      console.log(data);
    })
    window.location.reload();
  };

  SvuotaForm(form: NgForm): void {
    form.resetForm();
  }

  ngOnChanges(changes: SimpleChanges): void {
    
  };

  ngOnInit(): void {
    this.authService.GetUsername();
    this.username =  this.authService.GetUsername();
    this.usersService.GetSingle(this.username).subscribe(data=>{
      this.name = data.name;
      this.surname = data.surname;
      this.password = data.password;
      console.log(this.name + '' + this.surname + '' + this.password);
    });


   

  }

}
