import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Observable } from 'rxjs';
import { Reports } from '../models/reports.model';

@Injectable({
  providedIn: 'root'
})
export class ReportsService {

  constructor(private http: HttpClient) { }

  GetAllNew(): Observable<any> {
    return this.http.get('http://localhost:61558/api/Dashboard/Report/Nuovo');
  }
  GetAllOngoing(): Observable<any> {
    return this.http.get('http://localhost:61558/api/Dashboard/Report/In%20Gestione');
  }
  GetAllClosed(): Observable<any> {
    return this.http.get('http://localhost:61558/api/Dashboard/Report/Completato');
  }
  GetAllDeleted(): Observable<any> {
    return this.http.get('http://localhost:61558/api/Dashboard/DeletedReports');
  }

  GetSingle(reportId: number): Observable<any> {
    return this.http.get('http://localhost:61558/api/Dashboard/Get/' + reportId);
   }

  // Delete(EventoId: number): Observable<boolean> {
  //   return this.http.delete<boolean>("https://localhost:44390/api/Evento/Elimina" + EventoId);
  // }

  PutOngoing(reportId: number) {
    return this.http.put('http://localhost:61558/api/Dashboard/OngoingReport/' + reportId, {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    });
  }

  PutClosed(reportId: number) {
    return this.http.put('http://localhost:61558/api/Dashboard/CompletedReport/' + reportId, {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    });
  }

  PutDeleted(reportId: number) {
    return this.http.put('http://localhost:61558/api/Dashboard/VirtualDelete/' + reportId, {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    });
  }
}
