import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Injectable, Input } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Observable } from 'rxjs';
import { Utenti } from '../models/user.model';

@Injectable({
  providedIn: 'root'
})
export class CategoryService {

  constructor(private http: HttpClient) { }
  Create(p: any) {
    debugger;
    console.log(p);
    this.http.post("http://localhost:61558/api/Categories/Create", p, {
      headers: new HttpHeaders({
        "Content-Type": "application/json"
      })
    }).subscribe(response => {
      console.log(response);
      window.location.reload();
    }, err => {
    });
  }
  GetAll(): Observable<any> {
    return this.http.get("http://localhost:61558/api/Categories/GetAllWithRisk");
  }

  Delete(categoryId: number): Observable<boolean> {
    return this.http.delete<boolean>("http://localhost:61558/api/Categories/Delete/" + categoryId);
  }
}
