import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AppRoutingModule } from './app-routing.module';
import { NO_ERRORS_SCHEMA } from '@angular/core';
import { JwtHelperService, JwtModule, JWT_OPTIONS } from '@auth0/angular-jwt';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';
import { AppComponent } from './components/app/app.component';
import { LoginComponent } from './components/login/login.component';
import { AuthService } from './services/auth.service';
import { InterceptorService } from './services/interceptor.service';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { CommonModule } from '@angular/common';
import { SegnalazioniComponent } from './components/segnalazioni/segnalazioni.component';
import { LoaderComponent } from './components/loader/loader.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { NotificationpanelComponent } from './components/navbar/notificationpanel/notificationpanel.component';
import { SidebarComponent } from './components/sidebar/sidebar.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { SearchbarComponent } from './components/navbar/searchbar/searchbar.component';
import { UserdropComponent } from './components/navbar/userdrop/userdrop.component';
import { CategorieComponent } from './components/categorie/categorie.component';
import { CatlistComponent } from './components/categorie/catlist/catlist.component';
import { CatcontentComponent } from './components/categorie/catcontent/catcontent.component';
import { MainHeaderComponent } from './components/categorie/catcontent/main-header/main-header.component';
import { UtentiComponent } from './components/utenti/utenti.component';
import { UsercontentComponent } from './components/utenti/usercontent/usercontent.component';
import { UserHeaderComponent } from './components/utenti/usercontent/user-header/user-header.component';
import { UserFormComponent } from './components/utenti/usercontent/user-form/user-form.component';
import { CatformComponent } from './components/categorie/catcontent/catform/catform.component';
import { ManagerlistComponent } from './components/utenti/usercontent/list/managerlist/managerlist.component';
import { UserlistComponent } from './components/utenti/usercontent/list/userlist/userlist.component';
import { AdminlistComponent } from './components/utenti/usercontent/list/adminlist/adminlist.component';
import { SettingsComponent } from './components/settings/settings.component';
import { SettingscontentComponent } from './components/settings/settingscontent/settingscontent.component';
import { SettingsHeaderComponent } from './components/settings/settingscontent/settings-header/settings-header.component';
import { TabComponent } from './components/segnalazioni/tab/tab.component';
import { DashcontentComponent } from './components/dashboard/dashcontent/dashcontent.component';
import { LogComponent } from './components/dashboard/log/log.component';
@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    SegnalazioniComponent,
    LoaderComponent,
    DashboardComponent,
    NotificationpanelComponent,
    SidebarComponent,
    NavbarComponent,
    SearchbarComponent,
    UserdropComponent,
    CategorieComponent,
    CatlistComponent,
    CatcontentComponent,
    MainHeaderComponent,
    UtentiComponent,
    UsercontentComponent,
    UserlistComponent,
    UserHeaderComponent,
    UserFormComponent,
    CatformComponent,
    AdminlistComponent,
    ManagerlistComponent,
    SettingsComponent,
    SettingscontentComponent,
    SettingsHeaderComponent,
    TabComponent,
    DashcontentComponent,
    LogComponent,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    JwtModule,
    NgbModule,
    CommonModule
  ],
  providers: [
    {
      provide: JWT_OPTIONS,
      useValue: JWT_OPTIONS
    },
    JwtHelperService,
    AuthService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: InterceptorService,
      multi: true
    }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
