import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Observable } from 'rxjs';
import { Reports } from '../models/reports.model';

@Injectable({
  providedIn: 'root'
})
export class LogService {

  constructor(private http: HttpClient) { }

  GetLog(): Observable<any> {
    return this.http.get('http://localhost:61558/api/Log/LogList?rows=5');
  }
  DownloadLog(): Observable<any> {
    return this.http.get('http://localhost:61558/api/Log/DownloadLink');
  }
  GetStats(): Observable<any> {
    return this.http.get('http://localhost:61558/api/Query/CountUser');
  }
  GetCat(): Observable<any> {
    return this.http.get('http://localhost:61558/api/Query/CategoryCount');
  } 
  GetMonth(): Observable<any> {
    return this.http.get('http://localhost:61558/api/Query/Monthly');
  }
  GetNumber(): Observable<any> {
    return this.http.get('http://localhost:61558/api/Query/Percentage');
  }
}
