import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit, SimpleChanges } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { Categorie } from 'src/app/models/category.model';
import { AuthService } from 'src/app/services/auth.service';
import { CategoryService } from 'src/app/services/category.service';
import * as $ from 'jquery';

@Component({
  selector: 'app-catlist',
  templateUrl: './catlist.component.html',
  styleUrls: ['./catlist.component.scss']
})
export class CatlistComponent implements OnInit {

  username: string;
  category: Array<Categorie>;
  categoryId: number;
  name: string;
  riskLevel: number;
  p:any;
  constructor(public authService: AuthService, public categoryService: CategoryService, private router: Router) { }

  LogOut():void {
    this.authService.LogOut();
    this.authService.CheckUser();
  }

  Aggiorna(): void {
    this.categoryService.GetAll().subscribe(data => {
      this.category = data;
      console.log(this.category);
    }, (error: HttpErrorResponse) => {
      alert(error.error);
    });
  }

  AddCategory(form: NgForm): void {
    this.p={
      name: this.name,
      riskLevel: this.riskLevel
    }
    debugger;
      this.categoryService.Create(this.p);
    this.Aggiorna();
    this.SvuotaForm(form);
  }
  
  SvuotaForm(form: NgForm): void {
    form.resetForm();
  }

  DeleteCategory(categoryId: number) {
    this.categoryService.Delete(categoryId).subscribe(p => {
      this.Aggiorna();
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.Aggiorna();
  }

  
  ngOnInit(): void{
    
    $(window)
      .resize(function() {
        if ($(window).width() > 1090) {
          $('.sidebar').removeClass('collapse');
        } else {
          $('.sidebar').addClass('collapse');
        }
      })
      .resize();

   
    this.Aggiorna();
    this.authService.GetUsername();
    this.username =  this.authService.GetUsername();

  }

}
