import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/services/auth.service';
import { LogService } from 'src/app/services/log.service';
import { UsersService } from 'src/app/services/users.service';

@Component({
  selector: 'app-catcontent',
  templateUrl: './catcontent.component.html',
  styleUrls: ['./catcontent.component.scss']
})
export class CatcontentComponent implements OnInit {
total: number;

constructor(private router: Router, public logService: LogService) { }

getStats(): void {
    this.logService.GetCat().subscribe(data => {
      this.total = data;
    });
  }

  ngOnInit(): void {
    this.getStats();
  }

}
