import { Component, OnInit, SimpleChanges } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/services/auth.service';
import { UsersService } from 'src/app/services/users.service';
import * as $ from 'jquery';
import { Utenti } from 'src/app/models/user.model';
import { HttpErrorResponse } from '@angular/common/http';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-user-form',
  templateUrl: './user-form.component.html',
  styleUrls: ['./user-form.component.scss']
})
export class UserFormComponent implements OnInit {

  users: Array<Utenti>;
  utente: Utenti;
  userName: string;
  username: string;
  password: number;
  confirmPassword: number;
  name: string;
  surname: string;
  id: string;
  email: string;
  read: boolean;

  //query
  total: number;
  superAdmins: number;
  admins: number;
  basics: number;
  userlist: any = [];

  constructor(public authService: AuthService, public usersService: UsersService, private router: Router) { }


  AddUser(form: NgForm): void {
    this.usersService.RegisterUser(form);
    this.SvuotaForm(form);

  }

  AddAdmin(form: NgForm): void {
    this.usersService.RegisterAdmin(form);
    this.SvuotaForm(form);
  }

  AddManager(form: NgForm): void {
    this.usersService.RegisterManager(form);
    this.SvuotaForm(form);
  }

  SvuotaForm(form: NgForm): void {
    form.resetForm();
  }

  ngOnInit(): void {
  }
}
