import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { LoginComponent } from './components/login/login.component';
import { AuthGuard } from './guard/auth-guard.guard';
import { SegnalazioniComponent } from './components/segnalazioni/segnalazioni.component';
import { LoaderComponent } from './components/loader/loader.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { CategorieComponent } from './components/categorie/categorie.component';
import { UtentiComponent } from './components/utenti/utenti.component';
import { SettingsComponent } from './components/settings/settings.component';
import { SidebarComponent } from './components/sidebar/sidebar.component';
import { LogComponent } from './components/dashboard/log/log.component';

const routes: Routes = [
  //, canActivate: [AuthGuard]
  { path: 'dashboard', component: DashboardComponent, canActivate: [AuthGuard]},
  { path: 'segnalazioni', component: SegnalazioniComponent, canActivate: [AuthGuard]},
  { path: 'login', component: LoginComponent},
  { path: 'loader', component: LoaderComponent, canActivate: [AuthGuard]},
  { path: 'categorie', component: CategorieComponent, canActivate: [AuthGuard]},
  { path: 'navbar', component: NavbarComponent, canActivate: [AuthGuard]},
  { path: 'utenti', component: UtentiComponent, canActivate: [AuthGuard]},
  { path: 'settings', component: SettingsComponent, canActivate: [AuthGuard]},
  { path: 'sidebar', component: SidebarComponent, canActivate: [AuthGuard]},
  { path: 'log', component: LogComponent, canActivate: [AuthGuard]},

  { path: '', redirectTo: '/login', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
