import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Injectable, Input } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Observable } from 'rxjs';
import { Utenti } from '../models/user.model';

@Injectable({
  providedIn: 'root'
})
export class UsersService {

  constructor(private http: HttpClient) { }


  RegisterManager(form: NgForm) {

    const data = JSON.stringify(form.value);
    this.http.post('http://localhost:61558/api/Authentication/NewManager', data, {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }).subscribe(response => {

      window.location.reload();
      var x : any;
       x = response;
       window.alert('Credenziali Utente: \n' + 'Nome Utente: '+ x.username + ' '+'\nPassword: '+x.password);
      console.log(x);
      return response;
     }, err => {
       console.log(err);
     });
   }

  RegisterAdmin(form: NgForm) {

    const data = JSON.stringify(form.value);
    this.http.post('http://localhost:61558/api/Authentication/NewAdmin', data, {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }).subscribe(response => {

      // console.log('ciaone' , response);
     window.location.reload();
     var x : any;
      x = response;
      window.alert('Credenziali Utente: \n' + 'Nome Utente: '+ x.username + ' '+'\nPassword: '+x.password);
     console.log(x);
     return response;
    }, err => {
      console.log(err);
    });
  }

  RegisterUser(form: NgForm) {

    const data = JSON.stringify(form.value);
    this.http.post('http://localhost:61558/api/Authentication/NewUser', data, {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }).subscribe(response => {

      window.location.reload();
      var x : any;
       x = response;
       window.alert('Credenziali Utente: \n' + 'Nome Utente: '+ x.username + ' '+'\nPassword: '+x.password);
      console.log(x);
      return response;
     }, err => {
       console.log(err);
     });
   }

  Update(user: Utenti) {
    const data = JSON.stringify(user);
    return this.http.put('https://gruppo5pwuf3.azurewebsites.net/api/Authentication/editUser', data, {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    });
  }

  Put(val:any,val2:any) {
    // const data = JSON.stringify(val);
    return this.http.put('http://localhost:61558/api/ApplicationUser/ChangePW/'+ val,val2, {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    });
  }

  GetSingle(userName: string): Observable<any> {
    return this.http.get('http://localhost:61558/api/ApplicationUser/User/' + userName);
  }

  GetAllManager(): Observable<any> {
    return this.http.get('http://localhost:61558/api/ApplicationUser/Role/Manager');
  }
  GetAllAdmin(): Observable<any> {
    return this.http.get('http://localhost:61558/api/ApplicationUser/Role/Admin');
  }

  GetAllBasic(): Observable<any> {
    return this.http.get('http://localhost:61558/api/ApplicationUser/Role/Basic');
  }

  Delete(userName: string): Observable<boolean> {
    return this.http.delete<boolean>('http://localhost:61558/api/ApplicationUser/User/' + userName);
  }

  GetNumber(): Observable<any> {
    return this.http.get('http://localhost:61558/api/Query/CountUser');
  }

  UpgradeAdmin(userName: string):Observable<any> {
    return this.http.get('http://localhost:61558/api/Authentication/GiveAdmin/' + userName);
  }

}
