import { Component, OnInit, SimpleChanges } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/services/auth.service';
import { UsersService } from 'src/app/services/users.service';
import * as $ from 'jquery';
import { Utenti } from 'src/app/models/user.model';
import { HttpErrorResponse } from '@angular/common/http';
import { NgForm } from '@angular/forms';
import { LogService } from 'src/app/services/log.service';
@Component({
  selector: 'app-usercontent',
  templateUrl: './usercontent.component.html',
  styleUrls: ['./usercontent.component.scss']
})
export class UsercontentComponent implements OnInit {

  users: Array<Utenti>;
  utente: Utenti;
  userName: string;
  username: string;
  password: number;
  confirmPassword: number;
  name: string;
  surname: string;
  id: string;
  email: string;
  read: boolean;

  //query
  total: number;
  superAdmins: number;
  admins: number;
  basics: number;
  userlist: any = [];

  //  
  nuoviReports: number;
  nuovoPerc: number;
  reportsInGes: number;
  inGesPerc: number;
  reportsCompl: number;
  complPerc: number;
  statsList: any;
  hide1 = true;
  hide2 = true;
  hide3 = true;

  constructor(public authService: AuthService, public usersService: UsersService, private router: Router, public logService: LogService) { }

  LogOut(): void {
    this.authService.LogOut();
    this.authService.CheckUser();
  }

  AddUser(form: NgForm): void {
    this.usersService.RegisterUser(form);
    this.AggiornaManager();
    this.AggiornaAdmin();;
    this.SvuotaForm(form);

  }

  AddAdmin(form: NgForm): void {
    this.usersService.RegisterAdmin(form);
    this.AggiornaManager();
    this.AggiornaAdmin();
    this.SvuotaForm(form);

  }

  SvuotaForm(form: NgForm): void {
    form.resetForm();
  }

  AggiornaAdmin(): void {
    this.usersService.GetAllAdmin().subscribe(data => {
      this.users = data;
      console.log(this.users);
    }, (error: HttpErrorResponse) => {
      alert(error.error);
    });
  }
  AggiornaManager(): void {
    this.usersService.GetAllManager().subscribe(data => {
      this.users = data;
      console.log(this.users);
    }, (error: HttpErrorResponse) => {
      alert(error.error);
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.AggiornaManager();
    this.AggiornaAdmin();
    this.getStats();
  }
  // getUsernumber(): void {
  //   this.usersService.GetNumber().subscribe(data => {
  //     this.userlist = data;
  //     console.log(this.userlist);
  //   }, (error: HttpErrorResponse) => {
  //     alert(error.error);
  //   });
  // }
  DeleteUser(userName: string) {
    this.usersService.Delete(userName).subscribe(p => {
      this.AggiornaManager();
      this.AggiornaAdmin();
    });
  }

  EditUser(form: NgForm): void {
    // this.usersService.Put(form);
    this.AggiornaManager();
    this.AggiornaAdmin();
  }

  giveAdmin(username: string) {
    this.usersService.UpgradeAdmin(username).subscribe(p => {
      this.AggiornaManager();
      this.AggiornaAdmin();
    });
  }

  getStats(): void {
    this.logService.GetStats().subscribe(data => {
      this.statsList = data;
      this.total = this.statsList.total;
      this.superAdmins = this.statsList.superAdmins;
      this.admins = this.statsList.admins;
      this.basics = this.statsList.basics;
      console.log('ciao', this.statsList)
    });
  }

  enableDisableSuperadmin() {
    this.hide1 = !this.hide1;
  }

  enableDisableAdmin() {
    this.hide2 = !this.hide2;
  }

  enableDisableBasic() {
    this.hide3 = !this.hide3;
  }

  ngOnInit(): void {
    this.getStats();
  }
}
