import { Component, OnInit, SimpleChanges } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/services/auth.service';
import { UsersService } from 'src/app/services/users.service';
import * as $ from 'jquery';
import { Utenti } from 'src/app/models/user.model';
import { HttpErrorResponse } from '@angular/common/http';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-adminlist',
  templateUrl: './adminlist.component.html',
  styleUrls: ['./adminlist.component.scss']
})
export class AdminlistComponent implements OnInit {

  admin: Array<Utenti>;
  superadmin: Array<Utenti>;

  utente: Utenti;
  userName: string;
  username: string;
  password: number;
  confirmPassword: number;
  name: string;
  surname: string;
  id: string;
  email: string;
  read: boolean;

  //query
  total: number;
  superAdmins: number;
  admins: number;
  basics: number;
  userlist: any = [];

  constructor(public authService: AuthService, public usersService: UsersService, private router: Router) { }

  AggiornaAdmin(): void {
    this.usersService.GetAllAdmin().subscribe(data => {
      this.admin = data;
      console.log(this.admin);
    }, (error: HttpErrorResponse) => {
      alert(error.error);
    });
  }
  // AggiornaManager(): void {
  //   this.usersService.GetAllManager().subscribe(data => {
  //     this.superadmin = data;
  //     console.log(this.superadmin);
  //   }, (error: HttpErrorResponse) => {
  //     alert(error.error);
  //   });
  // }

  DeleteUser(userName: string) {
    this.usersService.Delete(userName).subscribe(p => {
      this.AggiornaAdmin();
      // this.AggiornaManager();
    });
  }

  giveAdmin(userName: string){
    this.usersService.UpgradeAdmin(userName).subscribe(p => {
      this.AggiornaAdmin();
      // this.AggiornaManager();
    });
  }
  

  ngOnChanges(changes: SimpleChanges): void {
    this.AggiornaAdmin();
    // this.AggiornaManager();
  }
  
  ngOnInit(): void {
    this.AggiornaAdmin();
    // this.AggiornaManager();
  }
}
