import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { analyzeAndValidateNgModules } from '@angular/compiler';
import { Injectable, Input } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { JwtHelperService } from '@auth0/angular-jwt';
import { Observable } from 'rxjs';
import { Utenti } from 'src/app/models/user.model';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  role?: any;
  username?: any;

  users: Array<Utenti>;

  @Input()
  isLogged: boolean;
  isNew: boolean;

  constructor(private http: HttpClient, private router: Router, private jwtHelper: JwtHelperService) {
    this.username = '';
    this.isLogged = false;
    this.isNew = true;
  }

  tokenGetter() {
    return localStorage.getItem('jwt');
  }

  CheckUser(): void {
    this.role = this.GetUserRole();
    this.username = this.GetUsername();
    const accessToken = this.tokenGetter();
    if (accessToken) {
      this.isLogged = true;
    }
    console.log('role: ' + this.role);
    console.log('logged?: ' + this.isLogged);
    console.log('username: ' + this.username);
  }

  LogOut() {
    this.CheckUser();
    this.router.navigate(['login']);
    return localStorage.removeItem('jwt');
  }

  GetInfo(username: string): void {
    this.GetUserInfo(username).subscribe(data => {
      this.users = data;
      this.isNew = data.isNewUser;
      console.log('isNew: ' + this.isNew);
      if (this.isNew){
        this.router.navigate(['settings']);
      } else {
        this.router.navigate(['segnalazioni']);
      }
      return this.isNew;
    });
  }

  GetUserInfo(username: string): Observable<any> {
    return this.http.get('http://localhost:61558/api/ApplicationUser/user/' + username);
  }

  GetUserRole() {
    const accessToken = this.tokenGetter();
    if (!accessToken) {
      return
    }
    const tokenData = this.jwtHelper.decodeToken(accessToken);
    const role = tokenData['http://schemas.microsoft.com/ws/2008/06/identity/claims/role'];
    return role;
  }

  GetUsername() {
    const accessToken = this.tokenGetter();
    if (!accessToken) {
      return
    }
    const tokenData = this.jwtHelper.decodeToken(accessToken);
    const username = tokenData['http://schemas.xmlsoap.org/ws/2005/05/identity/claims/name'];
    return username;
  }

  Login(form: NgForm) {
    const credentials = JSON.stringify(form.value);
    this.http.post('http://localhost:61558/api/Authentication/Login', credentials, {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }).subscribe(response => {
      const accessToken = (<any>response).accessToken;
      localStorage.setItem('jwt', accessToken);
      this.CheckUser();
      this.GetInfo(this.username);
    }, (err:HttpErrorResponse)=> {
      if(err.status==401){
        alert("Credenziali Errate");
      }
    });
  }

}
