import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Eventi } from 'src/app/models/log.model';
import { AuthService } from 'src/app/services/auth.service';
import { LogService } from 'src/app/services/log.service';
import * as $ from 'jquery';

@Component({
  selector: 'app-dashcontent',
  templateUrl: './dashcontent.component.html',
  styleUrls: ['./dashcontent.component.scss']
})
export class DashcontentComponent implements OnInit {


  log: Array<Eventi>;
  events: Eventi;
  logId: number;
  userName: string;
  actionDetails: string;
  logTime: Date;
  logList: any = [];
  statsList: any;
  downloadLink: string;
  nuoviReports: number;
  nuovoPerc: number;
  reportsInGes: number;
  inGesPerc: number;
  reportsCompl: number;
  complPerc: number;

  //mesi
  Gennaio: number;
  Febbraio: number;
  Marzo: number;
  Aprile: number;
  Maggio: number;
  Giugno: number;
  Luglio: number;
  Agosto: number;
  Settembre: number;
  Ottobre: number;
  Novembre: number;
  Dicembre: number;
  totale: number;

  constructor(public authService: AuthService, public logService: LogService, private router: Router) { }

  AggiornaLog(): void {
    this.logService.GetLog().subscribe(data => {
      this.logList = data;
      console.log(this.logList);
    });
  }

  DownloadLog(): void{
    this.logService.DownloadLog().subscribe(data => {
      this.downloadLink = data.downloadLink;
      console.log(this.downloadLink);
    });
  }

  getStats(): void{
    this.logService.GetNumber().subscribe(data => {
    this.statsList = data;
    this.complPerc = this.statsList.complPerc;
    this.reportsCompl = this.statsList.reportsCompl;
    this.nuoviReports = this.statsList.nuoviReports;
    this.reportsInGes = this.statsList.reportsInGes;
  });
}

getMonth(): void{
  this.logService.GetMonth().subscribe(data => {
  this.statsList = data;
  this.Gennaio = this.statsList.jan;
  this.Febbraio = this.statsList.feb;
  this.Marzo = this.statsList.mar;
  this.Aprile = this.statsList.apr;
  this.Maggio = this.statsList.may;
  this.Giugno = this.statsList.jun;
  this.Luglio = this.statsList.jul;
  this.Agosto = this.statsList.aug;
  this.Settembre = this.statsList.sep;
  this.Ottobre = this.statsList.oct;
  this.Novembre = this.statsList.nov;
  this.Dicembre = this.statsList.dec;
  this.totale = this.Gennaio + this.Febbraio + this.Marzo + this.Aprile + this.Maggio + this.Giugno + this.Luglio + this.Agosto + this.Settembre + this.Ottobre + this.Dicembre;
  console.log(this.statsList);
});
}

  ngOnInit(): void {
    this.AggiornaLog();
    this.getStats();
    this.getMonth();
    this.DownloadLog();


    $(document).ready(function($){
      var $timeline_block = $('.cd-timeline-block');
    
      //hide timeline blocks which are outside the viewport
      $timeline_block.each(function(){
        if($(this).offset().top > $(window).scrollTop()+$(window).height()*0.75) {
          $(this).find('.cd-timeline-img, .cd-timeline-content').addClass('is-hidden');
        }
      });
    
      //on scolling, show/animate timeline blocks when enter the viewport
      $(window).on('scroll', function(){
        $timeline_block.each(function(){
          if( $(this).offset().top <= $(window).scrollTop()+$(window).height()*0.75 && $(this).find('.cd-timeline-img').hasClass('is-hidden') ) {
            $(this).find('.cd-timeline-img, .cd-timeline-content').removeClass('is-hidden').addClass('bounce-in');
          }
        });
      });
    });


  }
}
